  .header-logo {
    flex: 0 0 60px;
    position: relative;
    z-index: 4; }
    .header-logo img {
      max-width: 100%;
      display: block; }
  .header-burger {
    display: none; }
  .header-nav {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .header-menu {
    display: flex;
