@media (max-width: 1101px) {
  .header body.lock {
    overflow: hidden; }
  .header-container {
    padding: 30px 20px; }
  .header-logo {
    flex: 0 0 40px; }
  .header-burger {
    display: block;
    position: relative;
    width: 50px;
    height: 35px;
    z-index: 56; }
    .header-burger span {
      position: absolute;
      background: linear-gradient(90deg, #4c9af5 0%, #5640df 50.52%, #9937bc 80.73%);
      left: 0;
      width: 100%;
      height: 3px;
      top: 15px;
      transition: all 0.3s ease 0s; }
    .header-burger::before {
      content: "";
      background: linear-gradient(90deg, #4c9af5 0%, #5640df 50.52%, #9937bc 80.73%);
      position: absolute;
      width: 100%;
      height: 3px;
      left: 0;
      transition: all 0.3s ease 0s;
      top: 0; }
    .header-burger::after {
      content: "";
      background: linear-gradient(90deg, #4c9af5 0%, #5640df 50.52%, #9937bc 80.73%);
      position: absolute;
      width: 100%;
      height: 3px;
      left: 0;
      transition: all 0.3s ease 0s;
      bottom: 0; }
    .header-burger.active span {
      transform: scale(0); }
    .header-burger.active::before {
      transform: rotate(45deg);
      top: 14px; }
    .header-burger.active::after {
      transform: rotate(-45deg);
      bottom: 17px; }
  .header-nav {
    display: block;
    position: fixed;
    top: 0;
    left: -100%;
    width: 80%;
    height: 100%;
    overflow: hidden;
    transition: all 0.3s ease 0s;
    background-color: #fff;
    box-shadow: 0px 5px 16px rgba(90, 90, 90, 0.25);
    padding: 150px 0 20px 0;
    z-index: 55; }
    .header-nav.active {
      left: 0; }
  .header-menu {
    display: block;
    padding: 0 10px; }
    .header-menu li {
      margin: 0 0 20px 0; }
    .header-menu__link {
      font-size: 24px; } }

